:root {
  --background: #fafafa;
  --foreground: #60697B;
  --heading: #343E51;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  background-image: linear-gradient(#f0f0f0 1px, transparent 1px),
    linear-gradient(90deg, #f0f0f0 1px, transparent 1px);
  background-size: 28px 28px;
  background-position: -1px -1px;
}
.blob {
  position: absolute;
  width: 220px;
  height: 220px;
  filter: blur(60px);
  opacity: 0.6;
  mix-blend-mode: multiply;
  animation: float 16s ease-in-out infinite alternate;
  transform: translateZ(0);
}
.blob--pink,
.blob--blue,
.blob--yellow,
.blob--yellow-light {
  background: none;
}
@keyframes float {
  0% {
    transform: translate3d(0, 0, 0) scale(1);
  }
  50% {
    transform: translate3d(12px, -18px, 0) scale(1.05);
  }
  100% {
    transform: translate3d(-10px, 16px, 0) scale(0.98);
  }
}
.nav-link:hover {
  animation: nav-shake 0.4s ease;
}
.fade-in {
  animation: fadeIn 0.8s ease both;
}
.fade-up {
  animation: fadeUp 0.9s ease both;
}
.fade-scale {
  animation: fadeScale 0.9s ease both;
}
.bounce-in {
  animation: bounceIn 0.8s ease-out both;
}
.typing {
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid rgba(102, 126, 234, 0.8);
  animation: typing 3s steps(38, end), caret 0.85s step-end infinite;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(16px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeScale {
  from {
    opacity: 0;
    transform: translateY(12px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.9) translateY(8px);
  }
  60% {
    opacity: 1;
    transform: scale(1.03) translateY(-4px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: rgba(102, 126, 234, 0.8);
  }
}
@keyframes nav-shake {
  0% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(-1.5px);
  }
  40% {
    transform: translateX(1.5px);
  }
  60% {
    transform: translateX(-1px);
  }
  80% {
    transform: translateX(1px);
  }
  100% {
    transform: translateX(0);
  }
}
